    {label:'',value:'cheat'},
    {label:'',value:'under_sync'},
    {
      text: '',
      subItems: [
        { label: '', value: 'data_clear' },
        { label: '', value: 'down_recover' },
        ]
    },
    {
      text: '',
      subItems: [
        { label: '', value: 'lock' },
        { label: '', value: 'unlock' },
      ]
    },
    {
      text: '',
      subItems: [
        { label: '', value: 'unstore_exception' },
      ]
    },
    {label:'',value:'service'},
    {label:'',value:'rollback'},
    {label:'',value:'update_vm_status'},
  ]
  private rows_operate_btns:any=[
    {label:'',value:'physical_detail'},
    {label:'',value:'migrate'},
    {label:'',value:'record'},
    {label:'',value:'resource'},
    {label:'',value:'remark'},
    {label:'',value:'service'},
    {label:'',value:'rollback'},
    {label:'',value:'update_host_status'}
  ]
  private error_msg={
    start_up_host:'',
    shutdown_host:'',
    restart_host:'',
    online_maintenance:'',
    offline_maintenance:'',
    finish:'',
    shelves:'',
    disperse:'',
    migrate:'',
    unlock:'',
  };
  private host_belongs=[]
  private search_data:any={}
  private page_info:any={
    current:1,
    size:20,
    total:0
  }
  private list:any=[];
  private visible:Boolean=false;
  private oper_type:string="";
  private oper_label:string="";
  private multi_rows:any=[];
  private multi_vm_rows:any=[];
  private current_host_id:'';
  private auth_list=[];
  private filter_data:any={}
  private host_types=[]
  private host_uses=[];
  private power_list=[];
  private machine_list=[]
  private host_source=[];
  private all_column_item=[];
  private all_item:Array<any>=[];
  private custom_visible:boolean = false;
  private tableHeight=70;
  private az_info:any={}
  private loading:boolean=false;
  private expand_rows:any=[]
  private new_prop_list:Array<string>=[];
  private filter_info:any={}
  private detail_id="";
  private detail_visible=false
  private timer = null
  private reason_list =[]
  private multiple_selection_id:Array<string> = []
  private ecs_fields:any=[
    {label:'ID',prop:'customer_id'},
    {label:'',prop:'customer_name'},
    {label:'ID',prop:'ecs_id'},
    {label:'',prop:'ecs_name'},
    {label:'',prop:'status'},
    {label:'IP',prop:'private_net'},
    {label:'IP',prop:'pub_net'},
    {label:'IP',prop:'pub_ip'},
    {label:'',prop:'ecs_info'},
    {label:'',prop:'disk',width:'160px'},
    {label:'',prop:'os_name'},
    {label:'',prop:'update_time'},
    {label:'',prop:'create_time'},
  ]
  private filed_name_list:Array<string>=[
        "supplier", "bios_version", "out_bond_version", "host_brand", "switch_one__name",
        "switch_two__name", "switch_three__name", "switch_four__name",
        "out_band_switch__name","net_model"
    ]
  private custom_host=[
    'host_name','host_ip','out_band_address','machine_status_name','power_status_name',
    'ecs_num','host_type_ch','host_purpose_ch','host_attribution_name','host_source',
    'cpu_model','gpu_model','gpu_count','net_nic','cpu','ram','hugepages_free','create_time'
  ]
  private backendList:any=[
    {value:'block',text:''},
    {value:'local',text:''},
    {value:'local,block',text:'/'}
  ]
